.right-block {
  position: relative;
  width: 540px;
  height: 150px;
  color: white;
  background-color: hsl(228, 56%, 26%);
  border-radius: 10px;
  padding-left: 40px;
  padding-top: 40px;
  padding-right: 40px;

  .right-block__text {
    margin-bottom: 17px;

    span {
      font-weight: bold;
    }
  }

  .right-block__flag {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 40px;
    top: -49px;
    width: 180px;
    height: 73px;
    background-color: white;
    border-radius: 10px;
    color: gray;
    text-transform: uppercase;
    font-weight: bold;
    text-align: center;

    span {
      font-size: 40px;
      margin-right: 5px;
      color: hsl(229, 57%, 11%);
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -18px;
      right: 0px;
      border-left: 20px solid transparent;
      border-right: 20px solid white;
      border-top: 20px solid transparent;
      border-bottom: 20px solid transparent;

      @media (max-width: 768px) {
        display: none;
      }
    }

    @media (max-width: 768px) {
      top: 50%;
      left: 50%;
      transform: translate(-50%, 65%);
    }
  }

  .progress-bar {
    position: relative;
    height: 18px;
    border: 3px solid hsl(229, 57%, 11%);
    border-radius: 10px;
    background-color: white;

    .fill {
      position: relative;
      width: 75%;
      height: 100%;
      background: linear-gradient(to right, hsl(6, 100%, 80%), hsl(335, 100%, 65%));
      border-radius: 10px;
      margin-bottom: 10px;

      .circle {
        position: absolute;
        width: 10px;
        height: 10px;
        top: 8%;
        right: 0;
        border: 1px solid gray;
        border-radius: 50%;
        background-color: white;
      }
    }

    .right-block__values {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      font-weight: bold;
    }
  }

  @media (max-width: 768px) {
    margin-bottom: 50px;
    width: 86%;
    padding: 32px;
  }
}