.left-block {
  width: 350px;
  height: 200px;
  background-color: hsl(228, 56%, 26%);
  padding-left: 40px;
  padding-top: 40px;
  margin-right: 30px;
  border-radius: 10px 95px 10px 10px;

  .left-block__buttons {
    display: flex;
  }

  .left-block__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background-color: hsl(229, 57%, 11%);
    margin-right: 13px;
    border-radius: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  @media (max-width: 780px) {
    margin-bottom: 18px;
    margin-right: 0;
    width: 86%;
  }
}